.text-field {
  display: flex;
  flex-direction: column;
  padding: 3px;
  margin: 0 5px;
  width: 100%;
  max-width: 100px;
  align-items: center;

  &__label {
    font-family: 'Roboto';
    color: rgb(87, 87, 87);
    font-size: 1rem;
    text-transform: uppercase;
    text-align: center;
  }

  &__input {
    user-select: none;
    text-align: center;
    width: 100%;
    height: 25px;
    max-width: 70px;
    font-family: 'Roboto';
    font-size: 1.2rem;
    border: none;
    border-bottom: 1px solid rgb(199, 199, 199);
    order: 1;
    transition: border-color 0.5s ease;
    outline: none;
    -moz-appearance: textfield;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      display: none;
    }

    &:hover {
      border-color: rgb(155, 155, 155);
    }

    &:active,
    &:focus {
      border-color: rgb(155, 155, 155);
    }
  }
}
