.thumb {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgb(85, 196, 153);
  z-index: 2;
  touch-action: none;

  &_horizontal {
    top: 0;
    transform: translate(0, -25%);

    &:hover,
    &:active {
      transform: translate(0, -25%) scale(1.2);
    }
  }

  &_vertical {
    left: 0;
    transform: translate(-25%, 0);

    &:hover,
    &:active {
      transform: translate(-25%, 0) scale(1.2);
    }
  }
}
